import utils from './utils'
require('../css/style.css');

const canvas = document.querySelector('canvas')
const c = canvas.getContext('2d')

canvas.width = innerWidth
canvas.height = innerHeight

const mouse = {
    x: innerWidth / 2,
    y: innerHeight / 2
}


const colors = ['#8ecae6', '#219EBC',  '#FFB703','#ff9671','#ffc75f','#f9f871','#fb8500'] //'#FFF6E5',

// Event Listeners
addEventListener('mousemove', (event) => {
    mouse.x = event.clientX
    mouse.y = event.clientY
})

addEventListener('resize', () => {
    canvas.width = innerWidth
    canvas.height = innerHeight

    init()
})




// Typed Text behavior
const textArray = ["udicr.","a mathematician.","a coder.","interested in numerical mathematics.","udicr."]
const typingDelay = 200;
const erasingDelay = 100;
const newTextDelay = 2000;

let textArrayIndex = 0;
let charIndex = 0;

const typedTextSpan = document.querySelector(".typed-text");
const cursorSpan = document.querySelector(".cursor");

function type(){
    if (charIndex<textArray[textArrayIndex].length){
        if (!cursorSpan.classList.contains("typing")) cursorSpan.classList.add("typing");
        typedTextSpan.textContent += textArray[textArrayIndex].charAt(charIndex);
        charIndex++;
        setTimeout(type,typingDelay);
    }else{
        cursorSpan.classList.remove("typing")
        setTimeout(erase,newTextDelay)
    }
}

function erase(){
    if (charIndex>0){
        if (!cursorSpan.classList.contains("typing")) cursorSpan.classList.add("typing");
        if (textArrayIndex <= textArray.length-2){
            typedTextSpan.textContent = textArray[textArrayIndex].substring(0,charIndex-1);
            charIndex--;
            setTimeout(erase,erasingDelay)
        }else{
            cursorSpan.classList.remove("typing")
        }

    }else{
        cursorSpan.classList.remove("typing")
        if(textArrayIndex>=textArray.length) {
            typedTextSpan.textContent = "udicr."
        }else{
            textArrayIndex+=0.5;
            setTimeout(type,typingDelay+1100);
        }
    }
}

document.addEventListener("DOMContentLoaded",function(){
    setTimeout(type,newTextDelay+250);
    type();
});
// Objects
class Body {
    constructor(id, x, y, dx, dy, mass, color) {
        this.id = id
        this.x = x
        this.y = y
        this.p = [x, y]
        this.v = [dx, dy]
        this.a = [0, 0]
        this.mass = mass
        this.radius = Math.pow(mass, 1 / 3) * 3 / (4*Math.PI) *rho
        this.color = color
        this.skip_counter = 0
    }

    draw() {
        c.beginPath()
        c.arc(this.p[0], this.p[1], this.radius, 0, Math.PI * 2, false)
        c.fillStyle = this.color
        c.fill()
        c.closePath()

    }

    update() {
        //
        if (this.id == -1){
            this.x = mouse.x
            this.y = mouse.y
            this.p = [this.x,this.y]

        }else{
            this.skip_counter = (this.skip_counter+1)%skip
            if (this.skip_counter===0){
                oldPoints.unshift(new Point(this.x,this.y,1,this.color))
            }
        }

        this.a = [0, 0]
        objects.forEach(object => {
            if (object.id !== this.id) {

                let d = utils.distance(this.p[0], this.p[1], object.p[0], object.p[1])

                // Collision detection goes here
                if (d<(this.radius+object.radius)){
                    //object lands on this and gets removed from list
                    this.mass += object.mass
                    this.radius = Math.pow(this.mass, 1 / 3) * 3 / (4*Math.PI) *rho
                    this.v[0] = (this.v[0]*this.mass + object.mass*object.v[0])/(this.mass+object.mass)
                    this.v[1] = (this.v[1]*this.mass + object.mass*object.v[1])/(this.mass+object.mass)
                    if (object.mass > this.mass){
                        this.color = object.color
                    }
                    let index = objects.indexOf(object)
                    objects.splice(index,1)
                    this.a[0] = 0
                    this.a[1] = 0
                } else{
                    this.a[0] += G * object.mass * (object.p[0] - this.p[0]) / Math.pow(d, 3)
                    this.a[1] += G * object.mass * (object.p[1] - this.p[1]) / Math.pow(d, 3)
                }
                }

        })
        this.v[0] += dt * this.a[0]
        this.v[1] += dt * this.a[1]
        this.p[0] += dt * this.v[0]
        this.p[1] += dt * this.v[1]
        if (Math.abs(this.p[0])>1.25*canvas.width || Math.abs(this.p[1])> 1.25*canvas.height){
            let index = objects.indexOf(this)
            objects.splice(index,1)
        }else{
            this.x = this.p[0]
            this.y = this.p[1]
            this.draw()
        }
    }
}

//OldPoints
class Point {
    constructor(x, y, radius, color) {

        this.x = x
        this.y = y
        this.radius = radius
        this.color = color
        this.age = 0
    }

    draw() {

        c.beginPath()
        c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false)
        c.fillStyle = this.color
        c.fill()
        c.closePath()

    }

    update() {
        this.age += 1
        if (this.age>tail_length){
            oldPoints.pop()
        }

    }
}



// Implementation
let rho = 2.0
let G = 6.67 * Math.pow(10, -2)
let dt = 0.1
let objects
let oldPoints
let tail_length = 200
let N
let v0 = 1.5
let skip = 4

function init() {
    //N
    N = Math.floor(canvas.width*canvas.height/30000)
    //Objects
    objects = []
    for (let i = 0; i < N; i++) {
        objects.push(
            new Body(i,
                utils.randomIntFromRange(0, canvas.width),
                utils.randomIntFromRange(0, canvas.height),
                utils.randomIntFromRange(-v0, v0),
                utils.randomIntFromRange(-v0, v0),
                utils.randomIntFromRange(500, 1000),
                utils.randomColor(colors)
            )
        )
    }
    let mouseObject;
    mouseObject = new Body(-1,mouse.x,mouse.y,0,0, 4000, 'yellow')
    objects.unshift(mouseObject)

    //old points
    oldPoints = []

}

//time-step measurement
//needed?

// Animation Loop

function animate() {
    requestAnimationFrame(animate)
    c.clearRect(0, 0, canvas.width, canvas.height)

    objects.forEach(object => {
        object.update()
    })
    oldPoints.forEach(point => {
        point.update()
    })
    oldPoints.forEach(point => {
        point.draw()
    })
    if (objects.length < 3){
        init()

    }


}

init()
animate()
